    height: 760px;
  }
}

@media only screen and (max-width: 540px) {
  .tiles .img {
    height: 300px;
  }
}

@media only screen and (max-width: 991px) {
  .tiles .first {
    grid-row: 2;
  }
}

@media only screen and (max-width: 991px) {
  .tiles .second {
    grid-row: 4;
  }
}

@media only screen and (max-width: 991px) {
  .tiles .third {
    grid-row: 6;
  }
}
